<template>
  <div>
    <v-list>
      <v-subheader v-if="!favorites.length">Your favorites list is empty</v-subheader>
      <div v-else>
        <v-subheader>Your favorites</v-subheader>
        <v-list-tile avatar v-for="dog in favorites" :key="dog.name" @click="{}">
          <v-list-tile-avatar>
            <img :src="dog.img">
          </v-list-tile-avatar>
          <v-list-tile-content>{{dog.name}}</v-list-tile-content>
          <v-list-tile-action>
            <v-icon @click="removeFromFavorites(dog)">delete</v-icon>
          </v-list-tile-action>
        </v-list-tile>
      </div>
    </v-list>
    <v-layout justify-center>
      <v-btn color="error" @click="clearFavorites">Clear Favorites</v-btn>
    </v-layout>
  </div>
</template>

<script>
import { mapActions } from 'vuex'

export default {
  computed: {
    favorites() {
      return this.$store.state.favorites
    }
  },
  methods: {
    ...mapActions(['removeFromFavorites', 'clearFavorites'])
  }
}
</script>

